.card {
	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
	transition: 0.3s;
	min-width: 40%;
	display: flex;
	flex-direction: column;
	max-width: 250px;
	margin: 0 auto;
}

.card:hover {
	box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.container {
  	padding: 2px 16px;
  	border-color: transparent;
}

.container>p{
	background-color: white;
	color: black;
}
